import { Injectable }    from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';
import { Observable }     from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { Hero } from '../heroes/hero';
import { MyUser } from '../accounts/account';
import { Router } from '@angular/router';

import { AccountsService } from '../_services/accounts.service';


@Injectable()
export class HeroService {
  private currentUser = localStorage.getItem('currentUser');
  private headers = new Headers();

  private heroesUrl : string = 'https://tourofheroes-atley.herokuapp.com/api/heroes';
  private isLoggedIn : boolean = false;
  constructor(private http: Http, private router: Router,private AccountsService: AccountsService,) {
      // this.headers.append({}"Authorization":"Token 5a6ef85f863b259e54ad0a2651f491674923b654")
      this.headers.append('Content-Type', 'application/json');

    this.headers.append('Authorization', `Token ${this.currentUser}`);

    }

  getisLoggedIn():boolean{
    if(localStorage.getItem('currentUser')){
    return true}
    else{return false}
  }

  setisLoggedIn(value:boolean):void{
    this.isLoggedIn=value;
  }

  getHeroes(): Promise<Hero[]> {
    const url = `${this.heroesUrl}/`
    return this.http.get(url,{headers: this.headers})
               .toPromise()
               .then(response => response.json() as Hero[])
               .catch(this.handleError);
  }


  getHero(id: number): Promise<Hero> {
    const url = `${this.heroesUrl}/${id}/`;
    return this.http.get(url,{headers: this.headers})
      .toPromise()
      .then(response => response.json() as Hero[])
      .catch(this.handleError);
  }

  delete(id: number): Promise<void> {
    const url = `${this.heroesUrl}/${id}/`;
    return this.http.delete(url, {headers: this.headers})
      .toPromise()
      .then(() => null)
      .catch(this.handleError);
  }

  create(name: string): Promise<Hero> {
    const url = `${this.heroesUrl}/`
    return this.http
      .post(url, JSON.stringify({name: name}), {headers: this.headers})
      .toPromise()
      .then(response => response.json() as Hero[])
      .catch(this.handleError);
  }

  update(hero: Hero): Promise<Hero> {
    const url = `${this.heroesUrl}/${hero.id}/`;
    return this.http
      .put(url, JSON.stringify(hero), {headers: this.headers})
      .toPromise()
      .then(() => hero)
      .catch(this.handleError);
  }

  search(term: string): Observable<Hero[]> {
    const url = `https://tourofheroes-atley.herokuapp.com/api/search/${term}/`;
    return this.http
               .get(url,{headers: this.headers})
               .map(response => response.json() as Hero[]);
  }
  // firstname:string,lastname:string,email:string,username:string,password:string
  // JSON.stringify({firstname:firstname,lastname:lastname,email:email,username:username,password:password}
  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}



